import { Component, OnInit } from '@angular/core';
import { ChatService } from '../../../services';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-chat2-dashboard',
  templateUrl: './chat2-dashboard.component.html',
  styleUrls: ['./chat2-dashboard.component.scss']
})

export class Chat2DashboardComponent implements OnInit {
  messages: Observable<any>;
  user: { photo: string };
  constructor(private service: ChatService) {}

  ngOnInit() {
    this.showMessage();
  }

  showMessage() {
    this.messages = this.service.routerChat('chat2');
    this.service.user(1).subscribe((data: string[]) => this.user = { photo: data[3]});
  }

}
